.AddTodo {
    margin: 10px;
    padding: 10px;
    text-align: center;
}
.AddTodo input {
    height: 25px;
    padding: 20px 0;
    width: 50%;
    margin: 20px;
    text-align: center;
    border-radius: 10px;
    box-shadow: 1px 1px 5px black;
    font-size: 30px;
    border: 0ch;
    background: #9E9E9E;
    color: whitesmoke;
}
.AddTodo button {
    padding: 20px;
    font-size: 24px;
    width: 300px
}


@media (max-width: 500px){
    .AddTodo input {
        height: 30px;
        width: 80%;
        font-size: 16px;
        padding: 6px;
    }
    .AddTodo button {
        padding: 3px;
        font-size: 12px;
        width: 80px
    }
}


@media (max-width: 400px){
    .AddTodo input {
        height: 40px;
        width: 90%;
        padding: 3px 0;
        font-size: 18px;
    }

    .AddTodo button {
        padding: 7px;
        font-size: 16px;
        width: 120px
    }
}