.Headers {
    background: #424242;
    margin-bottom: 10px;
    transition: 0.7s;
}

.Headers Button{
    font-size: 20px;
    color: white;
    height: auto;
    width: auto;
    padding: 12px;
    margin: 10px;
    transition: 0.7s;
}


@media (max-width: 500px) {
    .Headers Button{
        font-size: 18px;
        margin-bottom: 10px;
    }
}

@media (max-width: 400px) {
    .Headers Button{
        font-size: 18px;
        padding: 15px;
        margin: 5px;
    }
}

@media (max-width: 350px) {
    .Headers Button{
        font-size: 16px;
        padding: 10px;
    }
}